.modal-open {
    @media (max-width: 991px) {
        padding-right: 0 !important;
    }
}

.modal {
    z-index: 1300;
}

.modal-lg,
.modal-xl {
    @media (min-width: 992px) {
        max-width: 1080px;
    }
}

.modal-content {
    padding: 60px 105px;
    border-radius: 0;
    border: 0;
    background-color: $white;
    @media (max-width: 991px) {
        padding: 38px 20px 30px;
    }
    @media (max-width: 576px) {
        padding: 20px 15px 16px;
    }
}

.modal-header {
    border-radius: 0;
    border-bottom: 0;
    padding: 0;
    margin-bottom: 40px;
    @media (max-width: 991px) {
        margin-bottom: 64px;
    }
    @media (max-width: 576px) {
        margin-bottom: 40px;
    }
}

.modal-body {
    padding: 0;
    &__done-text {
        max-width: 311px;
        font-weight: normal;
        font-size: 15px;
        line-height: 140%;
        letter-spacing: -0.01em;
        color: transparentize($c-222222, 0.3);
        @media (max-width: 991px) {
            max-width: 290px;
            font-size: 14px;
            line-height: 152%;
        }
    }
    &__content {
        display: flex;
        flex-direction: column;
        padding: 50px;
        @media (max-width: 767px) {
            padding: 20px;
        }
    }
    &__title {
        font-family: Normalidad;
        font-style: normal;
        font-weight: bold;
        line-height: 140%;
        font-size: 25px !important;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        margin-bottom: 50px;
        @media (max-width: 767px) {
            font-size: 18px !important;
            margin-bottom: 25px;
        }
    }
    &__description {
        margin-bottom: 70px;
        @media (max-width: 767px) {
            margin-bottom: 35px;
        }
        & p {
            white-space: pre-wrap;
            margin: 0;
        }
    }
}

.modal-footer {
    border-radius: 0;
    border-top: 0;
    padding: 0;
    &__all-btns {
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    &__btn {
        width: 100%;
        height: auto;
        margin: 0;
        &:nth-child(2) {
            border-left: 1px solid transparentize($c-222222, 0.9);
        }
    }
}

.cross-modal {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 40px;
    height: 40px;
    z-index: 1;
    transition: 0.5s;
    cursor: pointer;
    transform: translate(0, 0);
    @media (max-width: 991px) {
        right: 20px;
    }
    @media (max-width: 576px) {
        top: 25px;
        right: 15px;
    }
    &:hover {
        opacity: 1;
        transition: 0.5s;
    }
}

.modal-title {
    font-family: "Normalidad";
    font-style: normal;
    font-weight: bold;
    font-size: 35px;
    line-height: 140%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    max-width: 420px;
    @media (max-width: 576px) {
        font-size: 19px;
        line-height: 136%;
        max-width: 220px;
    }
}

.form-types {
    & .__select {
        height: 38px;
    }
    & .__select__title {
        height: 38px;
        padding: 0 8px;
        border: 0;
        border-bottom: 1px solid transparentize($c-222222, 0.5);
        color: $c-868686;
        @media (max-width: 991px) {
            font-size: 15px;
            line-height: 22px;
            letter-spacing: 0.035em;
            padding: 0;
        }
        &::after {
            right: 0px;
        }
    }
    & .__select__content {
        border: 1px solid transparentize($c-222222, 0.5);
    }
}

.license-block {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    @media (max-width: 991px) {
        margin-top: 56px;
        justify-content: space-between;
    }
    @media (max-width: 576px) {
        margin-top: 40px;
        flex-direction: column-reverse;
        align-items: flex-start;
    }
    & .text {
        padding-right: 18px;
        max-width: 237px;
        margin-right: 16px;
        font-weight: normal;
        font-size: 12px;
        line-height: 130%;
        color: $c-222222;
        @media (max-width: 991px) {
            padding-right: 0;
        }
        @media (min-width: 577px) and (max-width: 991px) {
            margin-right: 16px;
        }
        @media (max-width: 576px) {
            max-width: 218px;
            margin-top: 16px;
        }
    }
}

// video modals
.video-modal {
    & .modal-dialog {
        @media (min-width: 768px) and (max-width: 1024px) {
            max-width: 700px;
        }
        @media (max-width: 576px) {
            margin: 0.5rem 15px;
        }
    }
    & .modal-header {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        margin-bottom: 0;
        background-color: transparent;
        padding: 0;
    }
    & .cross-modal {
        position: relative;
        width: 40px;
        top: unset;
        right: 0;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 10px;
        margin-left: auto;
        margin-right: 0;
        transform: translate(39px, 0);
        @media (max-width: 1199px) {
            top: 0;
            right: 0;
            transform: translate(0, 0);
        }
        & svg {
            & path {
                fill: $c-fbfbfb;
            }
        }
    }
    & .modal-content {
        padding: 0;
        border-radius: 0;
        border: 0;
        background-color: transparent;
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    & .modal-body {
        flex: 1;
    }
}

.video-iframe {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 60%;
    overflow: hidden;
    margin: 0 auto;
    & iframe,
    & video {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;
        margin: 0 auto;
    }
}

// photo modals
.photo-modal {
    & .modal-dialog {
        max-width: max-content;
        @media (max-width: 576px) {
            margin: auto;
        }
    }
    & .modal-header {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        margin-bottom: 0;
        background-color: transparent;
        padding: 0;
    }
    & .cross-modal {
        position: relative;
        width: 40px;
        top: unset;
        right: 0;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        margin-bottom: 10px;
        margin-left: auto;
        margin-right: 0;
        transform: translate(39px, 0);
        @media (max-width: 1199px) {
            top: 0;
            right: 0;
            transform: translate(0, 0);
        }
        & svg {
            & path {
                fill: $c-fbfbfb;
            }
        }
    }
    & .modal-content {
        padding: 0;
        border-radius: 0;
        border: 0;
        background-color: transparent;
        display: flex;
        flex-direction: column;
        width: 100%;
        @media (max-width: 576px) {
            padding: 0 15px;
        }
    }
    & .modal-body {
        flex: 1;
    }
    & .modal-footer {
        &>* {
            margin: 0;
        }
    }
}

.photo-place {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    min-width: 480px;
    height: 65vh;
    background-color: $c-222222;
    @media (max-width: 767px) {
        min-width: 260px;
        max-height: 80vh;
        height: 100%;
    }
    & img,
    & canvas,
    & svg {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: auto;
        height: 100%;
        margin: 0 auto;
        object-fit: cover;
        @media (max-width: 767px) {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
}

// успешное оповещение
