    & .modal-content {
        height: 558px;
        @media (max-width: 991px) {
            padding: 38px 20px;
            height: 721px;
        }
        @media (max-width: 576px) {
            padding: 20px 15px;
            height: 812px;
        }
    }
    & .modal-body {
        position: relative;
    }
