// popup speaker
.speaker-modal,
.event-modal {
    &__xl {
        & .modal-dialog {
            @media (min-width: 1200px) {
                max-width: 1380px !important;
            }
        }
    }
    & .modal-dialog {
        @media (min-width: 576px) {
            max-width: 520px;
        }
        @media (min-width: 768px) {
            max-width: 722px;
        }
    }
    & .modal-content {
        padding: 32px;
        @media (max-width: 767px) {
            padding: 20px 15px;
        }
    }
    & .modal-footer {
        position: absolute;
        bottom: -50px;
        left: 0;
        width: 100%;
        background-color: $c-fbfbfb;
    }
}

.speaker-grid {
    position: relative;
    display: grid;
    grid-template-columns: 185px 396px;
    column-gap: 32px;
    @media (max-width: 767px) {
        grid-template-columns: 1fr;
        row-gap: 24px;
    }
    & .social-share {
        position: absolute;
        top: 113px;
        right: -4px;
        @media (max-width: 991px) {
            right: -14px;
        }
        @media (max-width: 767px) {
            right: 2px;
        }
        @media (max-width: 576px) {
            right: -4px;
        }
    }
    &__left {
        @media (max-width: 767px) {
            // max-width: 165px;
            max-width: 100%;
        }
    }
    &__btns {
        margin-top: 60px;
        @media (max-width: 767px) {
            margin-top: 30px;
        }
    }
    &__wrapper {
        position: relative;
        width: 100%;
        height: 0;
        padding-bottom: 275px;
        margin-bottom: 18px;
        @media (max-width: 767px) {
            max-width: 165px;
        }
        & img,
        & canvas,
        & iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__industry {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        padding-bottom: 12px;
        margin-bottom: 15px;
        border-bottom: 1px solid transparentize($c-222222, 0.9);
        @media (max-width: 767px) {
            width: auto;
            max-width: fit-content;
            padding-right: 5px;
        }
        & .bg_point {
            width: 10px;
            height: 10px;
            flex: 0 0 10px;
        }
        &-title {
            margin-left: 5px;
            font-weight: normal;
            font-size: 12px;
            line-height: 130%;
            color: $c-222222;
            @media (max-width: 767px) {
                margin-left: 0;
            }
        }
    }
    &__range {
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: $c-868686;
    }
    &__bio {
        margin-top: 6px;
    }
    &__name {
        font-weight: 800;
        font-size: 18px;
        line-height: 155%;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        color: $c-222222;
        margin-bottom: 16px;
    }
    &__position {
        max-width: 276px;
        font-weight: normal;
        font-size: 11px;
        line-height: 16px;
        color: transparentize($c-222222, 0.5);
        margin-bottom: 32px;
    }
    &__biographi {
        max-width: 338px;
        font-weight: normal;
        font-size: 14px;
        line-height: 21px;
        color: $c-222222;
        margin-bottom: 66px;
    }
    &__event {
        &-title {
            font-weight: bold;
            font-size: 16px;
            line-height: 24px;
            color: $c-222222;
            margin-bottom: 32px;
        }
        &-item {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
            padding-bottom: 20px;
            margin-bottom: 20px;
            border-bottom: 1px solid transparentize($c-222222, 0.9);
            &:last-of-type {
                margin-bottom: 0;
                border-bottom: 0;
            }
        }
        &-time {
            font-weight: normal;
            font-size: 15px;
            line-height: 140%;
            color: $c-868686;
            margin-bottom: 8px;
        }
        &-desc {
            max-width: 300px;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            color: $c-222222;
            text-decoration: none;
            transition: 0.4s ease;
            &:hover {
                color: transparentize($c-222222, 0.5);
                text-decoration: none;
                transition: 0.4s ease;
            }
        }
    }
}

.speaker-flex {
    max-width: 571px;
    & .speaker-grid__industry {
        width: 100%;
        max-width: unset;
        border-bottom: unset;
        padding-bottom: unset;
        margin-bottom: unset;
        border-top: 1px solid transparentize($c-222222, 0.9);
        padding-top: 12px;
        margin-top: unset;
        @media (max-width: 767px) {
            margin-right: 50px;
        }
    }
}

// Восстановление пароля оповещение
.reset-modal {
    & .modal-content {
        padding: 0;
        & .modal-body {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 0 20px;
            height: 186px;
            @media (max-width: 399px) {
                padding: 0 15px;
            }
            & p {
                font-weight: normal;
                font-size: 15px;
                line-height: 140%;
                text-align: center;
                letter-spacing: -0.01em;
                color: transparentize($c-222222, 0.1);
                @media (max-width: 399px) {
                    font-size: 14px;
                    line-height: 130%;
                }
            }
        }
        & .modal-footer {
            height: 45px;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            border-top: 1px solid transparentize($c-222222, 0.9);
        }
    }
}

.close-notify {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    text-decoration: none;
    font-weight: bold;
    font-size: 13px;
    line-height: 13px;
    padding: 16px 0;
    margin: 0;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $c-222222;
    transition: 0.5s ease;
    &:hover {
        text-decoration: none;
        color: transparentize($c-222222, 0.4);
        transition: 0.5s ease;
    }
}

// Окно оповещения
.notify-modal {
    & .modal-content {
        padding: 0;
    }
    & .modal-body {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0 20px;
        height: 186px;
        @media (max-width: 399px) {
            padding: 0 15px;
        }
        & .title-notify {
            font-weight: bold;
            font-size: 17px;
            line-height: 160%;
            text-align: center;
            letter-spacing: -0.01em;
            color: $c-222222;
            margin-bottom: 10px;
        }
        & .text-notify {
            font-weight: normal;
            font-size: 15px;
            line-height: 140%;
            text-align: center;
            letter-spacing: -0.01em;
            color: transparentize($c-222222, 0.1);
            @media (max-width: 399px) {
                font-size: 14px;
                line-height: 130%;
            }
        }
    }
    & .modal-footer {
        height: 45px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        border-top: 1px solid transparentize($c-222222, 0.9);
    }
}

//Search modal
.modal--search {
    .modal-content {
        padding: 0;
    }
    .modal-body {
        padding-left: 12px;
        padding-right: 12px;
    }
    .modal-footer {
        border-top: 1px solid rgba(34, 34, 34, 0.1);
    }
    .fieldset {
        margin-top: 10px;
        margin-bottom: 10px !important;
        transition: all 0.2s ease-in-out;
        &.expanded {
            max-height: none;
        }
    }
    .search-form {
        border-bottom: 1px solid rgba(34, 34, 34, 0.1);
    }
    .search-form__input {
        background-color: #fff;
        color: #222;
    }
    @media (max-width: 767px) {
        .fieldset--grid {
            max-height: 110px;
            overflow: hidden;
        }
    }
    @media (min-width: 768px) {
        .modal-dialog {
            max-width: 640px;
        }
        .fieldset {
            margin-top: 20px;
            margin-bottom: 20px !important;
        }
    }
}

.search-results__item {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 16px 0;
    position: relative;
    &:not(:last-child) {
        border-bottom: 1px solid rgba(34, 34, 34, 0.1);
    }
    a {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }
}

.search-tags-expand {
    display: block;
    font-size: 14px;
    line-height: 20px;
    color: #868686;
    margin-bottom: 10px;
    @media (min-width: 768px) {
        display: none;
    }
}

.search-results__item-title {
    font-size: 15px;
    margin-bottom: 8px;
    width: 100%;
    @media (min-width: 768px) {
        margin-bottom: 0;
        width: auto;
    }
}

.search-results__item-category {
    font-size: 14px;
    line-height: 20px;
    color: #868686;
    @media (min-width: 768px) {
        margin-left: auto;
    }
}

.search-results__empty {
    font-size: 15px;
    margin-top: 16px;
    margin-bottom: 40px;
}

#modalAuth {
    & .modal-body {
        padding-top: 52px;
        padding-bottom: 32px;
        height: 100%;
    }
    & .login-grid {
        display: grid;
        grid-template-columns: 1fr;
        row-gap: 52px;
        margin-bottom: 20px;
    }
    & .register-grid {
        display: grid;
        grid-template-columns: 1fr;
        row-gap: 52px;
        margin-bottom: 60px;
    }
    & .forgot-area {
        margin-bottom: 30px;
    }
    & .forgot-link {
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: transparentize($c-868686, 0.4);
        text-decoration: none;
        transition: 0.4s ease;
        &:hover {
            color: $c-fbfbfb;
            text-decoration: none;
            transition: 0.4s ease;
        }
    }
    & .btns-area {
        margin-bottom: 32px;
    }
    & .footer-area {
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: transparentize($c-222222, 0.4);
        &>a {
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            color: transparentize($c-222222, 0.4);
        }
    }
    & .select-types {
        margin-top: 15px;
    }
    & .forgot-h1 {
        @media (min-width: 992px) {
            font-size: 35px;
        }
    }
}

.project-modal {
    & .modal-dialog {
        @media (max-width: 1199px) {
            max-width: 860px;
        }
    }
    & .modal-content {
        padding: 50px 110px;
        @media (max-width: 767px) {
            padding: 28px 15px;
        }
    }
    & .modal-header {
        margin-bottom: 50px;
    }
    & .modal-title {
        max-width: 860px;
        font-family: Normalidad;
        font-style: normal;
        font-weight: bold;
        font-size: 35px;
        line-height: 140%;
        letter-spacing: 0.02em;
        text-transform: uppercase;
        @media (max-width: 767px) {
            max-width: 450px;
            font-size: 19px;
            line-height: 136%;
        }
        @media (max-width: 576px) {
            padding-right: 40px;
        }
    }
}